<template>
	<view class="main">
		<h3>标题：{{dataDeatil.title}}</h3>
		<p>节点名称：{{dataDeatil.node_name}}</p>
		<p>上报时间：{{dataDeatil.add_time}}</p>
		<p>状态：{{dataDeatil.status===0?'待处理':(dataDeatil.status===1?'处理中':'已处理')}}</p>  <!--0待处理1处理中2已处理-->
		<p>类型：{{dataDeatil.type===0?'巡检上报':(dataDeatil.type===1?'水质上报':'水环境上报')}}</p>
		<view>描述：{{dataDeatil.describe}}</view>
		<view v-for="item in dataDeatil.image_arr">
			<imgae class="img"  :src="item" ></imgae>
		</view>

	</view>
</template>
<script>
	import {getReportDetail} from "@/utils/api/comment"
	export default {
		components: {},
		data() {
			return {
				dataDeatil:{}
			}
		},
		onLoad() {
			this.id = this.$Route.query.id
			this.getReportDetail({id:this.id})
		},
		methods: {
			async getReportDetail(params){
				let res = await getReportDetail(params)
				if(res.errno ===0){
					this.dataDeatil = res.data
					console.log(this.dataDeatil);
				}else {

				}
			},
		},

	}
</script>

<style scoped lang="scss">
	.main{
		background-color: #FFFFFF;
		border-radius: 10rpx;
		margin: 20rpx;
		padding: 20rpx;
		line-height: 2;
	}
</style>
